.container {
  border: solid 1px #aaa;
  border-radius: 20px;
  padding: 8px;
  margin: 8px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  &:hover {
    opacity: 0.5;
  }
  p {
    font-size: 100px;
  }
}
.title {
  margin: 0;
  color: #aaa;
}
.button {
  background-color: #ddd;
  border: none;
  padding: 8px;
  border-radius: 8px;
  &:hover {
    opacity: 0.5;
    // background-color: #aaa;
    // color: #fff;
    // cursor: pointer;
  }
}